.service {
	margin: 60px 0 80px;

	.col-lg-8 {
		padding-right: 40px;
	}

	.main-title {
		font-size    : 34px;
		color        : #2B2B2B;
		font-weight  : 700;
		margin-bottom: 65px;
	}

	.main-content {
		margin-top: 35px;

		figure {
			margin: 35px 0;

			img {
				width: 100%;
			}
		}

		p {
			font-size    : 18px;
			color        : #4D4D4D;
			margin-bottom: 25px;

			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	.social-media {
		margin-top: 40px;
	}
}