.tintuc-chitiet {
	margin: 60px 0 20px;

	.col-lg-8 {
		padding-right: 40px;
	}

	.main-title {
		font-size    : 34px;
		color        : #2B2B2B;
		margin-bottom: 20px;
	}

	.time-news {
		display       : inline-block;
		font-size     : 13px;
		letter-spacing: 4px;
	}

	.main-content {
		margin-top: 50px;

		figure {
			margin: 50px 0;

			img {
				width: 100%;
			}
		}

		p {
			font-size    : 18px;
			color        : #4D4D4D;
			margin-bottom: 25px;

			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	.social-media {
		margin-top: 40px;
	}
}