.request {
	margin: 60px 0 122px;

	.col-lg-8 {
		padding-right: 40px;
	}

	.form-row {
		margin: 0 -10px;
	}

	h3 {
		font-size    : 34px;
		color        : #2B2B2B;
		margin-bottom: 45px;
	}

	button {
		float: right;
	}

	.form-group {
		position     : relative;
		padding      : 0 10px;
		margin-bottom: 20px;

		&.small {
			padding: 0 5px;
		}

		input[type='text'] {
			width      : 100%;
			font-size  : 16px;
			color      : #707070;
			font-style : italic;
			line-height: 45px;
			padding    : 0 20px;
			border     : 1px solid #C1C1C1;
			font-style : italic;
		}

		input[type='number'] {
			width       : 100%;
			font-size   : 16px;
			color       : #707070;
			font-style  : italic;
			line-height : 45px;
			padding-left: 20px;
			border      : 1px solid #C1C1C1;
			font-style  : italic;
		}

		input[type='date'] {
			display   : block;
			color     : #707070;
			border    : 1px solid #C1C1C1;
			padding   : 10px 15px;
			width     : 100%;
			height    : 45px;
			font-size : 16px;
			font-style: italic;
		}

		textarea.small {
			font-size: 16px;
			resize   : none;
			padding  : 15px;
			width    : 100%;
			height   : 90px;
			color    : #707070;
			border   : 1px solid #C1C1C1;
		}

		textarea.big {
			resize : none;
			padding: 15px;
			width  : 100%;
			height : 160px;
			color  : #707070;
			border : 1px solid #C1C1C1;
		}

		label {
			cursor       : pointer;
			width        : 100%;
			font-size    : 18px;
			color        : #2B2B2B;
			margin-bottom: 10px;

			&.color-red {
				color: #BC2E31;
			}

			&.block {
				color      : #FFFFFF;
				line-height: 45px;
				padding    : 0 15px;
				background : #3E4095;
			}
		}

		select {
			width     : 100%;
			background: none;
			height    : 45px;
			color     : #707070;
			border    : 1px solid #C1C1C1;
			padding   : 10px 15px;
			font-style: italic;
		}

		table {
			border: 1px solid #C1C1C1;

			th,
			td {
				border: 1px solid #C1C1C1;
			}

			th {
				font-size : 16px;
				color     : white;
				background: #3e4095;

				text-align: center;
				height    : 50px;
				padding   : 10px 15px;
			}

			tr {
				&:nth-child(2) {
					td {
						&.tac-vu {
							.delete {
								display: none;
							}
						}
					}
				}
			}

			td {
				&:nth-child(1) {
					width: 130px;
				}

				&:nth-child(2) {
					width: 100px;
				}

				&:nth-child(3) {
					width: 80px;
				}

				&:nth-child(4) {
					width: 60px;
				}

				&:nth-child(5) {
					width: 60px;
				}

				&:nth-child(6) {
					width: 105px;
				}

				&:nth-child(7) {
					width: 115px;
				}

				select,
				input {
					border: none !important;
				}

				&.tac-vu {
					width     : 81px;
					text-align: center;

					img {
						cursor : pointer;
						display: inline-block;
						margin : 0 5px;
					}
				}
			}
		}
	}
}